import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    padding : 20px;
`;
let Title = styled.h4`
    font-size:25px;
    color: ${props => props.color};
`;


// class Detail2 extends React.Component {
//     componentDidMount(){

//     }

//     componentWillUnmount(){

//     }
// }

function Detail(p) {

    let [show,chgShow] =  useState(true);
    let [inputData,chgInputData] = useState('');

    useEffect(()=>{
        let timer = setTimeout(() => {
            chgShow(false);
        }, 2000);

        return ()=>{ clearTimeout(timer) }
    }, [] );
    
    let { id } = useParams();
    let history = useHistory();
    let detail = p.shoes.find(function(v){
        return v.id == id;
    })
    return (
            <div className="container">
            <Box>
                {/* <Title color={'blue'}>상세페이지</Title> */}
                <Title className="red">상세페이지</Title>
            </Box>
            
            {/* { inputData } */}
            {/* <input onChange={ (e)=>{ chgInputData(e.target.value)} }/> */}

            {
                (show === true) 
                ? <Alert/>
                : null
            }
            <div className="row">
                <div className="col-md-6">
                <img src={ 'https://codingapple1.github.io/shop/shoes'+(detail.id+1)+'.jpg' } width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                <h4 className="pt-5"> {detail.title} </h4>
                <p>{detail.content}</p>
                <p>{detail.price}</p>

                <Info stock={p.stock}></Info>
        
                <button className="btn btn-danger" onClick={ ()=>{ p.chgStock([9,11,12]) } }>주문하기</button> &nbsp;
                <button className="btn btn-danger" onClick={ ()=>{ history.goBack(); } }>뒤로가기</button> 
                </div>
            </div>
            </div> 
    )
}

function Info(props) {
    return (
        <p>
            재고 : {props.stock[0]}
        </p>
    )
}

function Alert() {
    return (
    <div className="my-alert2">
        <p>재고가 얼마 남지 않았습니다.</p>
    </div>  
    )
}

export default Detail;